<template>
    <div class="container my-4">
        <div class="card my-4 border-0">
            <div class="card-header text-center bg-dark text-white">
                <span v-if="checkAlarm" class="text-white"><i class="fa fa-bell" aria-hidden="true"></i></span>
                <strong> PANEL</strong>
                <span class="float-right" v-if="isMaster">Total: {{ totalAssets }}</span>
            </div>
            
            <div class="my-2 mx-4">
                <table class="table table-sm text-center">
                    <thead>
                        <th colspan="2">
                            Lista de Alertas
                            <span class="">
                                <button class="btn btn-sm" v-bind:class="{ 'btn-outline-primary': isAlertAudioAlarm, 'btn-outline-secondary' : !isAlertAudioAlarm }" v-on:click="alertAudioToggle">
                                    <i class="fa fa-bell" aria-hidden="true" v-if="isAlertAudioAlarm"></i>
                                    <i class="fa fa-bell-slash" aria-hidden="true" v-if="!isAlertAudioAlarm"></i>
                                </button>
                            </span>
                        </th>
                        <th colspan="2">
                            Lista de Acciones
                            <span class="">
                                <button class="btn btn-sm" v-bind:class="{ 'btn-outline-primary': isActionAudioAlarm, 'btn-outline-secondary' : !isActionAudioAlarm }" v-on:click="actionAudioToggle">
                                    <i class="fa fa-bell" aria-hidden="true" v-if="isActionAudioAlarm"></i>
                                    <i class="fa fa-bell-slash" aria-hidden="true" v-if="!isActionAudioAlarm"></i>
                                </button>
                            </span>
                        </th>
                    </thead>
                    <tbody>
                        <tr>
                            <td>
                                <div 
                                    v-for="alert in buyAlertList" 
                                    v-bind:key="alert">
                                    <span class="badge badge-success"> {{ alert }}</span>
                                </div>
                            </td>
                            <td>
                                <div 
                                    v-for="alert in sellAlertList" 
                                    v-bind:key="alert">
                                    <span class="badge badge-danger"> {{ alert }}</span>
                                </div>
                            </td>
                            <td>
                                <div 
                                    v-for="action in buyActionList" 
                                    v-bind:key="action">
                                    <span class="badge badge-success"> {{ action }}</span>
                                </div>
                            </td>
                            <td>
                                <div 
                                    v-for="action in sellActionList" 
                                    v-bind:key="action">
                                    <span class="badge badge-danger"> {{ action }}</span>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div class="accordion" id="accordionInstrument">
            <instrument-component 
                v-for="instrument in instruments" 
                v-bind:key="instrument.instrument" 
                v-bind:instrument="instrument"
                />
        </div>
    </div>    
</template>

<script>
import InstrumentComponent from './../components/InstrumentComponent'

export default {
    name: 'dashboard',
    components: {
        'instrument-component': InstrumentComponent,
    },
    data(){
        return {
            instruments: [],
            isActionAudioAlarm: true,
            isAlertAudioAlarm: false,
        }
    },
    methods: {
        playSound () {
            var audio = new Audio('http://soundbible.com/mp3/Elevator Ding-SoundBible.com-685385892.mp3');
            audio.play();
        },
        
        actionAudioToggle(){
            this.isActionAudioAlarm = !this.isActionAudioAlarm
        },

        alertAudioToggle(){
            this.isAlertAudioAlarm = !this.isAlertAudioAlarm
        }
    },
    computed: {
        sellAlertList(){
            const filterArr = this.instruments.filter(item => item.alertType == 'SELL')
            const result = filterArr.map(item => item.instrument)
            return result
        }, 
        buyAlertList(){ 
            const filterArr = this.instruments.filter(item => item.alertType == 'BUY')
            const result = filterArr.map(item => item.instrument)
            return result
        }, 
        sellActionList(){
            const filterArr = this.instruments.filter(item => item.actionType == 'SELL')
            const result = filterArr.map(item => item.instrument)
            return result
        },
        buyActionList(){
            const filterArr = this.instruments.filter(item => item.actionType == 'BUY')
            const result = filterArr.map(item => item.instrument)
            return result
        },
        checkAlarm(){
            if(this.isActionAudioAlarm){
                if(this.sellActionList.length > 0 || this.buyActionList.length > 0){
                    this.playSound()
                    return true
                } else {
                    return false
                }
            } else if(this.isAlertAudioAlarm){
                if(this.sellAlertList.length > 0 || this.buyAlertList.length > 0){
                    this.playSound()
                    return true
                } else {
                    return false
                }
            }
            return false
        },
        totalAssets(){
            return this.instruments.length
        },
        isMaster(){
            return this.$store.state.isMaster
        }
    },
    sockets:{
        connect(){
            this.connected = true
        },
        disconnect(){
            this.connected = false
        },
        fxData(fxdata){
            this.instruments = fxdata
        }
    }
}
</script>

