import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        isLogged: false,
        isMaster: false,
        username: null,
    },
    mutations: {
        LOGIN(state, xAuth) {
            window.localStorage.setItem('binarySignalToken', JSON.stringify(xAuth))
            this.dispatch('checkToken')
        },
        LOGOUT(state) {
            state.username = null
            state.isMaster = false
            state.isLogged = false
            window.localStorage.removeItem('binarySignalToken')
        },
        CHECK_TOKEN(state){
            const token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
            if(token){
                axios.post('/users/me', {}, {
                    headers: {
                    'Content-Type': 'application/json',
                    'X-Auth': token.token
                    }
                }).then(resp => {
                    if(resp.data.user){
                        state.username = resp.data.user.username
                        state.isMaster = resp.data.user.isMaster
                        state.isLogged = true
                    } else {
                        state.username = null
                        state.isLogged = false
                        state.isMaster = false
                        window.localStorage.removeItem('binarySignalToken')
                    }
                }).catch(() => {
                    state.username = null
                    state.isLogged = false
                    state.isMaster = false
                    window.localStorage.removeItem('binarySignalToken')
                });
            } else {
                state.username = null;
                state.isLogged = false;
                state.isMaster = false;
            }
        }
    },
    actions: {
        checkToken(context){
            // context.commit('CHECK_TOKEN')
            const token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
            if(token){
                axios.post('/users/me', {}, {
                    headers: {
                    'Content-Type': 'application/json',
                    'X-Auth': token.token
                    }
                }).then(resp => {
                    if(resp.data.user){
                        context.state.username = resp.data.user.username
                        context.state.isMaster = resp.data.user.isMaster
                        context.state.isLogged = true
                    } else {
                        context.state.username = null
                        context.state.isLogged = false
                        context.state.isMaster = false
                        window.localStorage.removeItem('binarySignalToken')
                    }
                }).catch(() => {
                   context.state.username = null
                    context.state.isLogged = false
                    context.state.isMaster = false
                    window.localStorage.removeItem('binarySignalToken')
                });
            } else {
                context.state.username = null;
                context.state.isLogged = false;
                context.state.isMaster = false;
            }
        }
    }
})

export default store