<template>
    <div class="card mb-2 border-0 my-2">
        <div class="card-header border" v-bind:class="candleClass">
            {{candle.timeframe}} - {{ candle.time }}
        </div>
        <div class="card-body py-0 px-0">

            <!-- ohlc data section-->
            <table class="table table-bordered table-sm my-2">
                <thead>
                    <tr class="table-secondary">
                        <th>O</th>
                        <th>H</th>
                        <th>L</th>
                        <th>C</th>
                    </tr>
                </thead>
                <tbody>
                    <tr class="light">
                        <td>{{ candle.open.toFixed(5) }}</td>
                        <td>{{ candle.high.toFixed(5) }}</td>
                        <td>{{ candle.low.toFixed(5) }}</td>
                        <td>{{ candle.close.toFixed(5) }}</td>
                    </tr>
                </tbody>
            </table>
            <!-- end ohlc data section -->

            <!-- stochastic data section -->
            <table class="table table-bordered table-sm my-2">
                <thead>
                    <tr class="table-secondary">
                        <th colspan="2">Stoch(14)</th>
                        <th colspan="2">Stoch(6)</th>
                    </tr>
                </thead>
                <tbody>
                    <tr class="light">
                        <td>{{ candle.indicatorStochLong.stoch.toFixed(2) }}</td>
                        <td>{{ candle.indicatorStochLong.smooth.toFixed(2) }}</td>
                        <td>{{ candle.indicatorStochShort.stoch.toFixed(2) }}</td>
                        <td>{{ candle.indicatorStochShort.smooth.toFixed(2) }}</td>
                    </tr>
                </tbody>
            </table>
            <!-- end stochastic data section -->

            <!-- stochRsi and Rsi data section -->
            <table class="table table-bordered table-sm my-2">
                <thead>
                    <tr class="table-secondary">
                        <th colspan="2">StochRsi(14)</th>
                        <th>Rsi(14)</th>
                    </tr>
                </thead>
                <tbody>
                    <tr class="light">
                        <td>{{ candle.indicatorStochRsi.stoch.toFixed(2) }}</td>
                        <td>{{ candle.indicatorStochRsi.smooth.toFixed(2) }}</td>
                        <td>{{ candle.indicatorRsi.toFixed(2) }}</td>
                    </tr>
                </tbody>
            </table>
            <!-- end stochRsi and Rsi data section -->

            <!-- bollinger band data section -->
            <table class="table table-bordered table-sm my-2">
                <thead>
                    <tr class="table-secondary">
                        <th colspan="3">Bollinger(34)</th>
                    </tr>
                </thead>
                <tbody>
                    <tr class="light">
                        <td>{{ candle.indicatorBolBand.lowerBand.toFixed(5) }}</td>
                        <td>{{ candle.indicatorBolBand.bolBand.toFixed(5) }}</td>
                        <td>{{ candle.indicatorBolBand.upperBand.toFixed(5) }}</td>
                    </tr>
                </tbody>
            </table>
            <!-- end bollinger band data section -->

            <!-- prev candle stochastic data section -->
            <table class="table table-bordered table-sm my-2">
                <thead>
                    <tr class="table-secondary">
                        <th colspan="2">Prev. Stoch(14)</th>
                        <th colspan="2">Prev. Stoch(6)</th>
                    </tr>
                </thead>
                <tbody>
                    <tr class="light">
                        <td>{{ candle.prev.indicatorStochLong.stoch.toFixed(2) }}</td>
                        <td>{{ candle.prev.indicatorStochLong.smooth.toFixed(2) }}</td>
                        <td>{{ candle.prev.indicatorStochShort.stoch.toFixed(2) }}</td>
                        <td>{{ candle.prev.indicatorStochShort.smooth.toFixed(2) }}</td>
                    </tr>
                </tbody>
            </table>
            <!-- end prev candle stochastic data section -->
            <!-- stochRsi and Rsi data section -->
            <table class="table table-bordered table-sm my-2">
                <thead>
                    <tr class="table-secondary">
                        <th colspan="2">Prev. StochRsi(14)</th>
                    </tr>
                </thead>
                <tbody>
                    <tr class="light">
                        <td>{{ candle.prev.indicatorStochRsi.stoch.toFixed(2) }}</td>
                        <td>{{ candle.prev.indicatorStochRsi.smooth.toFixed(2) }}</td>
                    </tr>
                </tbody>
            </table>
            <!-- end stochRsi and Rsi data section -->

            <!-- moving average data section -->
            <table class="table table-bordered table-sm my-2">
                <thead>
                    <tr class="table-secondary">
                        <th>Sma</th>
                        <th>Ema</th>
                        <th>Wma</th>
                    </tr>
                </thead>
                <tbody>
                    <tr class="light">
                        <td>{{ candle.indicatorSma.toFixed(5) }}</td>
                        <td>{{ candle.indicatorEma.toFixed(5) }}</td>
                        <td>{{ candle.indicatorWma.toFixed(5) }}</td>
                    </tr>
                </tbody>
            </table>
            <!-- end moving average data section -->

            <!-- awesome oscilator data section -->
            <table class="table table-bordered table-sm my-2">
                <thead>
                    <tr class="table-secondary">
                        <th>Awesome Osc</th>
                    </tr>
                </thead>
                <tbody>
                    <tr class="light">
                        <td>{{ candle.indicatorAwesomeOsc.toFixed(5) }}</td>
                    </tr>
                </tbody>
            </table>
            <!-- end awesome oscilator data section -->

        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {

        }
    },
    props: {
        candle: Object
    },
    computed: {
        candleClass(){
            return {
                'bg-danger text-white': this.candle.signal == 'SELL',
                'bg-success text-white': this.candle.signal == 'BUY',
                'bg-dark text-white': this.candle.signal == 'NEUTRAL',
            }
        },
    }
}
</script>
